<template>
  <div class="login_container">
    <div class="login_box">
      <el-form ref="login" class="login_form" :model="loginForm">
        <p class="header">ÁôªÂΩï</p>
        <el-form-item label="ÈÇÆÁÆ±">
          <el-input v-model="loginForm.username" type="email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="ÂØÜÁ†Å">
          <el-input v-model="loginForm.password" type="password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item class="btn">
          <el-button @click="login" type="primary">ÁôªÂΩï</el-button>
          <el-button @click="register">
              Ê≥®ÂÜå
          </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    login() {
      const vm = this;
      let config = {
      //formData  Êèê‰∫§ËØ∑Ê±ÇÂ§¥Êúâ‰∏§ÁßçÊñπÂºè multipart/form-data  Âíå application/x-www-form-urlencoded
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let params = new URLSearchParams();
      params.append('username', vm.loginForm.username);
      params.append('password', vm.loginForm.password);
      this.$http.post("http://localhost:8888/demo_war_exploded/LoginServlet", params, config)
        .then(function(response) {
          if(response.status == 200){
            console.log("success");
            vm.$message.success('ÁôªÂΩïÊàêÂäü');
            window.sessionStorage.setItem('token', window.sessionStorage.getItem('SERVER_TOKEN'));
            window.sessionStorage.setItem('uname', vm.loginForm.username);
            vm.$router.push('/home');
          } else {
            vm.$message.error('ÁôªÂΩïÂ§±Ë¥•');
          }
          console.log(response);
        })
        .catch(function(error) {
          vm.$message.error('ÁôªÂΩïÂ§±Ë¥•');
          console.log(error);
          console.log("error");
        });
    },

    register() {
      this.$router.push('/register');
    }
  }
};
</script>

<style lang="less" scoped>
.login_container {
  background-color: #f0f0f0;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 350px;
  background-color: #fff;
  border-radius: 6px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.login_form {
  position: absolute;
  bottom: 0px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.header {
  font-size: 21px;
  font-weight: bold;
}

.btn {
  position: relative;
  float: right;
}
</style>